
import './App.css';
import Header from "./MyComponents/Header";
import {Todos} from "./MyComponents/Todos";
import {AddTodo} from "./MyComponents/AddTodo";
import {Footer} from "./MyComponents/Footer";
import {About} from "./MyComponents/About";
import React, { useState , useEffect} from 'react';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

function App() {

let initTodo;
if(localStorage.getItem("todos")===null){
  initTodo = [];
}
else{
  initTodo = JSON.parse(localStorage.getItem("todos"));
}

  const onDelete = (todo) =>{
    console.log("i am ondelete", todo);
// below code write for deleting todo
    setTodos(todos.filter((e)=>{
      return e!==todo;
    }))
    localStorage.setItem("todos", JSON.stringify(todos));
  }

  const addTodo=(title, desc)=>{
    console.log("i am doing adding to this todo", title , desc)
    let sno;
    if(todos.length===0){
      sno=1;
    }
    else{
   sno = todos[todos.length-1].sno + 1;
    }

  const myTodo = {
    sno: sno,
    title : title,
    desc: desc,
  }
  setTodos([...todos, myTodo]);
  console.log(myTodo);

  }

  const [todos, setTodos] = useState(initTodo
    // {
    //   sno: 1,
    //   title: "Go to the markets" ,
    //   desc : "You need go to the market 1"
    // },
    // {
    //   sno: 2,
    //   title: "Go to the mall" ,
    //   desc : "You need go to the market2 "
    // },
    // {
    //   sno: 3,
    //   title: "Go to the ghat" ,
    //   desc : "You need go to the market 3 "
    // },
   );
   useEffect(() => {
    localStorage.setItem("todos", JSON.stringify(todos));
  
  }, [todos])
  return (
    <>
     <Router>
    <Header title="My Todos list" searchBar={true} />

    <Switch>
          <Route exact path="/" render={()=>{
            return(
            <>
                    <AddTodo addTodo={addTodo} />
                    <Todos todos={todos} onDelete={onDelete} />
                    </>)
          }}>
          </Route>
          <Route exact path="/about">
          <About />
          </Route>         
        </Switch>
    <Footer/>
    </Router>
    </>
  );
}

export default App;
